.App {
  text-align: center;
}

.justify-content-center{
  background:rgb(230, 243, 247);
  height:80px;
}
.nav-item{
  width:100px;
  margin-top:10px;
}
.nav-item a{
  color:rgb(0, 184, 251);
  font-size:20px;
  padding:10px;
}
.nav-link:hover{
  color:#333;
  border-bottom:2px solid #333;
}
.git{
  width:40px;
}
/* home */
.home-bg{
  height: calc(100vh - 80px);
  background-image:url(./img/pagebg.jpg);
  background-size:cover;
  background-position:center;
}

.start{
  opacity:0;
}

.end{
  opacity:1;
  transition: opacity 1s;
}

/* profile */
.menubar{
  display:flex;
  justify-content:center;
}
.menu{
  margin:200px 50px 50px 50px;
  background-color:white;
  height:200px;
  width:350px;
  border:2px solid rgb(196, 246, 246);
  border-radius:50px;
}
.menu h2{
  margin-top:60px;
}
.menu h2:hover{
  cursor:pointer;
}
.menu:hover{
  background-color:rgb(196, 246, 246);
  color:#fff;
}

/* 자기소개 */
/* 기술스텍 */
.prof2{
  display:flex;
  justify-content:center;
}
.circle{
  width:150px;
  height:150px;
  background-color:tomato;
  border-radius:50%;
  margin:100px;
}
.circle h2{
  padding-top:60px;
  font-size:25px;
}
/* project */
.card-img-top{
  width:95%;
}
.card-group{
  justify-content:center;
}
.card{
  border-radius:20px;
}
.cardtable{
  display:flex;
  width:25%;
  margin:40px;
}

/* 수료과정 */
.prof4 img{
  width:30%;
}

/* contact */
img{
  width:20%;
  margin:10px;
  border-radius:25%;
}

h2{
  padding:20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
